import React, { useState, useEffect } from "react";
import axios from "axios";
import styled from "styled-components";
import AdminHeader from "../../components/AdminHeader";

const AdminFilter = () => {
  const [region, setRegion] = useState("경기");
  const [title, setTitle] = useState("");
  const [contentId, setContentId] = useState([]);
  const [value, setValue] = useState(0);
  const [searchRegion, setSearchRegion] = useState("전국");
  const [searchData, setSearchData] = useState([]);
  const [openModal, setOpenModal] = useState(false);
  const [modalData, setModalData] = useState([]);
  const [rsRegion, setRsRegion] = useState("경기");
  const [rsOpenModal, setRsOpenModal] = useState(false);
  const [checkedList, setCheckedList] = useState([]);

  useEffect(() => {
    const getData = async () => {
      await axios({
        method: "get",
        url: `${process.env.REACT_APP_API_URL}/admin/filter/getList?region=${searchRegion}`,
      })
        .then((res) => {
          setSearchData(res.data.filters);
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    };
    getData();
  }, [searchRegion]);

  const titleSearchEvent = async () => {
    await axios({
      method: "post",
      url: `${process.env.REACT_APP_API_URL}/admin/filter/search1`,
      data: {
        title: title,
      },
    })
      .then((res) => {
        setModalData(res.data.dataList);
        setOpenModal(true);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const regionSearchEvent = async () => {
    await axios({
      method: "post",
      url: `${process.env.REACT_APP_API_URL}/admin/filter/searchRegion1`,
      data: {
        region: rsRegion,
        curPage: 1,
      },
    })
      .then((res) => {
        console.log(res.data);
        setModalData(res.data.dataList);
        setRsOpenModal(true);
        setCheckedList([]);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const enterEvent = (e) => {
    if (e.key == "Enter") {
      titleSearchEvent();
    }
  };

  const modalClickEvent = (id, region) => {
    setContentId(id);
    setRegion(region);
    setOpenModal(false);
  };

  const handleSelect = (e) => {
    setRegion(e.target.value);
  };
  const onChangeTitle = (e) => {
    setTitle(e.target.value);
  };
  const onChangeContentId = (e) => {
    setContentId(e.target.value);
  };
  const onChangeValue = (e) => {
    setValue(e.target.value);
  };

  const handleSearchSelect = (e) => {
    setSearchRegion(e.target.value);
  };
  const handleRsSelect = (e) => {
    setRsRegion(e.target.value);
  };

  const onCheckedItem = (checked, contentid) => {
    if (checked) {
      setCheckedList((prev) => [...prev, contentid]);
    } else if (!checked) {
      setCheckedList(checkedList.filter((el) => el !== contentid));
    }
  };

  const countEvent = async (cal) => {
    console.log(contentId.split(","));
    if (window.confirm("연산하시겠습니까?")) {
      await axios({
        method: "post",
        url: `${process.env.REACT_APP_API_URL}/admin/filter/adjust1`,
        data: {
          region: region,
          contentids: contentId.split(","),
          value: value,
          status: cal,
        },
      })
        .then(async (res) => {
          await axios({
            method: "get",
            url: `${process.env.REACT_APP_API_URL}/admin/filter/getList?region=${searchRegion}`,
          })
            .then((res) => {
              setSearchData(res.data.filters);
            })
            .catch((err) => {
              console.log(err);
            });
        })
        .catch((err) => {
          console.log(err);
        });
    } else {
      return;
    }
  };

  const confirmEvent = () => {
    setRegion(rsRegion);
    setRsOpenModal(false);
    setContentId(checkedList.join());
  };
  return (
    <Container>
      <AdminHeader />
      <FixedContainer>
        <CenteredText>여행대로 검색량 조정 페이지</CenteredText>
        <FindIdArea>
          <ContentArea>
            <ContentLabel>제목으로 검색</ContentLabel>
            <TitleInputArea>
              <TitleInput type="text" onChange={onChangeTitle} onKeyUp={enterEvent} />
              <TitleSearchBtn onClick={() => titleSearchEvent()}>검색</TitleSearchBtn>
            </TitleInputArea>
          </ContentArea>
          <ContentArea>
            <ContentLabel>지역으로 검색</ContentLabel>
            <SelectSearchArea>
              <RegionSelect onChange={handleRsSelect} value={rsRegion}>
                <option value="경기">경기</option>
                <option value="서울">서울</option>
                <option value="경북">경북</option>
                <option value="인천">인천</option>
                <option value="부산">부산</option>
                <option value="제주">제주</option>
                <option value="강원">강원</option>
                <option value="충남">충남</option>
                <option value="경남">경남</option>
                <option value="전남">전남</option>
                <option value="충북">충북</option>
                <option value="대전">대전</option>
                <option value="전북">전북</option>
                <option value="대구">대구</option>
                <option value="광주">광주</option>
                <option value="울산">울산</option>
                <option value="세종">세종</option>
              </RegionSelect>
              <TitleSearchBtn onClick={() => regionSearchEvent()}>검색</TitleSearchBtn>
            </SelectSearchArea>
          </ContentArea>
        </FindIdArea>
        <InputContainer>
          <ContentArea>
            <ContentLabel>지역</ContentLabel>
            <RegionSelect onChange={handleSelect} value={region}>
              <option value="경기">경기</option>
              <option value="서울">서울</option>
              <option value="경북">경북</option>
              <option value="인천">인천</option>
              <option value="부산">부산</option>
              <option value="제주">제주</option>
              <option value="강원">강원</option>
              <option value="충남">충남</option>
              <option value="경남">경남</option>
              <option value="전남">전남</option>
              <option value="충북">충북</option>
              <option value="대전">대전</option>
              <option value="전북">전북</option>
              <option value="대구">대구</option>
              <option value="광주">광주</option>
              <option value="울산">울산</option>
              <option value="세종">세종</option>
            </RegionSelect>
          </ContentArea>
          <ContentArea>
            <ContentLabel>장소 아이디</ContentLabel>
            <ContentIdInput type="text" onChange={onChangeContentId} value={contentId} />
          </ContentArea>
          <ContentArea>
            <ContentLabel>숫자</ContentLabel>
            <CountInput type="number" onChange={onChangeValue} />
          </ContentArea>
          <ContentArea>
            <ContentLabel>연산</ContentLabel>
            <CountBtnArea>
              <CountBtn onClick={() => countEvent(0)}>적용</CountBtn>
              <CountBtn onClick={() => countEvent(1)}>곱하기</CountBtn>
              <CountBtn onClick={() => countEvent(2)}>나누기</CountBtn>
            </CountBtnArea>
          </ContentArea>
        </InputContainer>
      </FixedContainer>
      <SearchContainer>
        <RegionSelect
          onChange={handleSearchSelect}
          value={searchRegion}
          style={{ marginBottom: 20 }}
        >
          <option value="전국">전국</option>
          <option value="경기">경기</option>
          <option value="서울">서울</option>
          <option value="경북">경북</option>
          <option value="인천">인천</option>
          <option value="부산">부산</option>
          <option value="제주">제주</option>
          <option value="강원">강원</option>
          <option value="충남">충남</option>
          <option value="경남">경남</option>
          <option value="전남">전남</option>
          <option value="충북">충북</option>
          <option value="대전">대전</option>
          <option value="전북">전북</option>
          <option value="대구">대구</option>
          <option value="광주">광주</option>
          <option value="울산">울산</option>
          <option value="세종">세종</option>
        </RegionSelect>
        <SearchLabelArea>
          <SearchTitleLabel>제목</SearchTitleLabel>
          <SearchCountLabel>검색량</SearchCountLabel>
          <SearchCotentIdLabel>장소 아이디</SearchCotentIdLabel>
        </SearchLabelArea>
        {searchData?.map((el, idx) => {
          return (
            <SearchDataArea key={idx}>
              <SearchTitle>{el.title}</SearchTitle>
              <SearchCount>{el.count}</SearchCount>
              <SearchContentId>{el.contentid}</SearchContentId>
            </SearchDataArea>
          );
        })}
      </SearchContainer>
      {/* 제목으로 검색 시 모달 */}
      {openModal ? (
        <ModalBackground>
          <ModalContainer>
            <ModalCloseArea>
              <CloseBtn onClick={() => setOpenModal(false)}>닫기</CloseBtn>
            </ModalCloseArea>
            <ModalListContainer>
              <ModalListLabel>
                <ModalListRegion>지역</ModalListRegion>
                <ModalListTitle>제목</ModalListTitle>
                <ModalListCategory>카테고리</ModalListCategory>
                <ModalListAddr>주소</ModalListAddr>
                <ModalListCount>검색량</ModalListCount>
                <ModalListContentId>장소 아이디</ModalListContentId>
              </ModalListLabel>
              {modalData.map((el, idx) => {
                return (
                  <ModalListArea
                    key={idx}
                    onClick={() => modalClickEvent(el.contentid, el.regionName)}
                  >
                    <ModalListRegion>{el.regionName}</ModalListRegion>
                    <ModalListTitle>{el.title}</ModalListTitle>
                    <ModalListCategory>{el.describe}</ModalListCategory>
                    <ModalListAddr>{el.addr1}</ModalListAddr>
                    <ModalListCount>{el.count}</ModalListCount>
                    <ModalListContentId>{el.contentid}</ModalListContentId>
                  </ModalListArea>
                );
              })}
            </ModalListContainer>
          </ModalContainer>
        </ModalBackground>
      ) : (
        <></>
      )}
      {/* 지역으로 검색시 모달 */}
      {rsOpenModal ? (
        <ModalBackground>
          <ModalContainer>
            <ModalCloseArea>
              <ConfirmBtn onClick={() => confirmEvent()}>완료</ConfirmBtn>
              <CloseBtn onClick={() => setRsOpenModal(false)}>닫기</CloseBtn>
            </ModalCloseArea>
            <ModalListContainer>
              <ModalListLabel>
                <ModalListRank>순위</ModalListRank>
                <ModalListTitle>제목</ModalListTitle>
                <ModalListCategory>카테고리</ModalListCategory>
                <ModalListAddr>주소</ModalListAddr>
                <ModalListCount>검색량</ModalListCount>
                <ModalListContentId>장소 아이디</ModalListContentId>
                <ModalListSelect>선택</ModalListSelect>
              </ModalListLabel>
              {modalData.map((el, idx) => {
                return (
                  <ModalListArea key={idx} style={{ cursor: "inherit" }}>
                    <ModalListRank>{idx + 1}</ModalListRank>
                    <ModalListTitle>{el.title}</ModalListTitle>
                    <ModalListCategory>{el.describe}</ModalListCategory>
                    <ModalListAddr>{el.addr1}</ModalListAddr>
                    <ModalListCount>{el.count}</ModalListCount>
                    <ModalListContentId>{el.contentid}</ModalListContentId>
                    <ModalListSelect>
                      <input
                        type="checkbox"
                        style={{ width: 24, height: 24, cursor: "pointer" }}
                        onChange={(e) => {
                          onCheckedItem(e.target.checked, el.contentid);
                        }}
                      />
                    </ModalListSelect>
                  </ModalListArea>
                );
              })}
            </ModalListContainer>
          </ModalContainer>
        </ModalBackground>
      ) : (
        <></>
      )}
    </Container>
  );
};
